:root {
  color-scheme: light dark;
}

.bg-body {
  background-color: var(--bg-body-color);
}

.switcher {
  border: none;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 2px;
  display: grid;
  position: absolute;
  top: 10px;
  right: 18px;
}

.switcher__legend {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.switcher__radio {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  z-index: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 16px;
  margin: 0;
  transition: filter .2s ease-in;
}

.switcher__radio:focus {
  outline: none;
}

.switcher__radio--light {
  background-image: url("light.b762eab7.svg");
}

.switcher__radio--auto {
  background-image: url("auto.37eb201e.svg");
}

.switcher__radio--dark {
  background-image: url("dark.d5332de6.svg");
}

.switcher__status {
  background-color: var(--bg-status-library);
  background-image: url("status.3af589db.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px;
  border-radius: 18px;
  transition: background-position .2s ease-in;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 0 2px #0003;
}

@media screen and (min-width: 768px) {
  .switcher {
    top: 12px;
    right: 30px;
  }
}

.switcher__radio:focus-visible ~ .switcher__status {
  box-shadow: 0 0 0 2px #000;
}

.switcher__radio--light:checked ~ .switcher__status {
  background-position: 2px 50%;
}

.switcher__radio--auto:checked ~ .switcher__status {
  background-position: center;
}

.switcher__radio--dark:checked ~ .switcher__status {
  background-position: right 2px center;
}

.header-wrapper .switcher {
  top: -25px;
  right: 50px;
}

.header-wrapper .switcher__status {
  background-color: var(--bgc-status-main);
}

@media screen and (min-width: 768px) {
  .header-wrapper .switcher {
    right: 60px;
  }
}

